import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class Mesoer extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const { className, children, ...others } = props;
		const klassName = KlassName('layout-mesoer',className);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

Mesoer.propTypes = {
	className: PropTypes.string,
	children: PropTypes.node,
};

Mesoer.defaultProps = {
};

export default Mesoer;